import React from 'react'
import styled from 'styled-components'
const More = () => {
    return (
        <Divi>
            More
        </Divi>
    )
}

export default More
const Divi=styled.div`
font-size:16px;
width:100%;
border:1px solid black;
text-align:center;
background:lightgrey;`;